/* ---------------------------------------
   Question Card 레이아웃
---------------------------------------- */
.question-card {
  background: #ffffff;
  padding: 28px;
  border-radius: 22px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
  animation: fadeIn 0.25s ease;
}

/* fade animation */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(8px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ---------------------------------------
   Header
---------------------------------------- */
.q-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
}

.chapter-label {
  display: flex;
  align-items: center;
  gap: 8px;
}

.chapter-index {
  font-size: 18px;
  font-weight: 700;
  color: #6c63ff;
}

.chapter-name {
  font-size: 18px;
  font-weight: 600;
  color: #333;
}

.q-progress-group {
  font-size: 16px;
  background: #f3f2ff;
  padding: 6px 14px;
  border-radius: 12px;
  color: #6c63ff;
  font-weight: 600;
}

/* ---------------------------------------
   Question Text
---------------------------------------- */
.q-text {
  font-size: 22px;
  font-weight: 700;
  color: #333;
  margin-bottom: 22px;
  line-height: 1.45;
}

/* ---------------------------------------
   객관식 옵션 버튼
---------------------------------------- */
.q-options {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.q-option {
  padding: 16px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 14px;
  background: #f5f5ff;
  border: 2px solid transparent;
  cursor: pointer;
  transition: 0.25s ease;
}

.q-option:hover {
  background: #ebe8ff;
}

.q-option.selected {
  background: #6c63ff;
  color: #fff;
  border: 2px solid #4f45d7;
}

/* ---------------------------------------
   주관식 텍스트 입력
---------------------------------------- */
.q-textarea {
  width: 100%;
  height: 140px;
  border-radius: 14px;
  border: 2px solid #ddd;
  padding: 16px;
  font-size: 18px;
  resize: none;
  outline: none;
  transition: 0.25s ease;
}

.q-textarea:focus {
  border-color: #6c63ff;
}

/* ---------------------------------------
   Footer: Back / Skip / Next 버튼
---------------------------------------- */
.q-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 28px;
  width: 100%;
}

/* 버튼 공통 */
.q-btn,
.back-btn,
.skip-btn,
.next-btn {
  width: 120px;
  height: 48px;
  border-radius: 14px;
  font-size: 17px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: 0.25s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* -------------------------
   BACK (보라색, 투명도 50%)
-------------------------- */
.back-btn {
  background: rgba(155, 89, 182, 0.5);
  color: white;
  border: 2px solid rgba(155, 89, 182, 0.6);
}

.back-btn:hover {
  background: rgba(155, 89, 182, 0.7);
  border-color: rgba(155, 89, 182, 0.9);
}

/* -------------------------
   SKIP (노란색, 투명도 50%)
-------------------------- */
.skip-btn {
  background: rgba(241, 196, 15, 0.5);
  color: #4a4300;
  border: 2px solid rgba(241, 196, 15, 0.6);
  margin: 0 auto; /* 버튼 가운데 정렬 */
}

.skip-btn:hover {
  background: rgba(241, 196, 15, 0.7);
  border-color: rgba(241, 196, 15, 0.9);
}

/* -------------------------
   NEXT (파란색)
-------------------------- */
.next-btn {
  background: rgba(52, 152, 219, 0.3); /* 비활성 */
  color: rgba(255, 255, 255, 0.4);
}

.next-btn.active {
  background: rgb(52, 152, 219);
  color: #fff;
}

.next-btn.active:hover {
  background: rgb(41, 128, 185);
}

/* ---------------------------------------
   반응형
---------------------------------------- */
@media (max-width: 480px) {
  .q-footer {
    flex-direction: column;
    gap: 12px;
  }

  .q-btn,
  .back-btn,
  .skip-btn,
  .next-btn {
    width: 100%;
  }

  .chapter-label {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }
}
